h1 {
    font-family: Arial, sans-serif;
    font-size: 2.5rem; /* Responsive font size */
    font-style: italic;
    font-weight: bold;
    color: white;
    position: absolute; /* Positioning the h1 element */
    top: 5%; /* Adjust to place it at the desired position within the outer box */
    left: 25%; /* Center horizontally within the outer box */
    transform: translateX(-50%); /* Align the text properly in the center */
    z-index: 2; /* Ensure it is above other elements */
    text-align: center; /* Ensure text stays centered */
}

h2 {
    font-family: Arial, sans-serif;
    font-style: italic;
    font-weight: normal;
    font-size: 1.8rem;
}

h3 {
    font-family: Arial, sans-serif;
    font-style: italic;
    font-weight: lighter;
    color: #0072b1;
    text-decoration: underline #0072b1;
    font-size: 1.5rem;
}

p {
    font-family: Arial, sans-serif;
    font-size: 1rem;
    font-style: italic;
    font-weight: normal;
    color: white;
}

.text {
    padding: 1rem 2rem; /* Responsive padding */
}

.my-outer-box {
    position: absolute;
    background-color: rgb(25 42 69 / 0.8);
    width: 90%; /* Adjust width to be relative */
    max-width: 820px; /* Cap the width on larger devices */
    height: 70%; /* Adjust height to be relative */
    max-height: 550px; /* Cap the height on larger devices */
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.my-inner-box {
    background-color: #eb86b1;
    width: 85%; /* Responsive width */
    height: 65%; /* Responsive height */
    margin-top: 4.5rem;
    border-radius: 25px;
    overflow: auto;
}

.profile-pic {
    position: absolute;
    object-fit: cover;
    background-color: white;
    width: 10rem; /* Responsive size */
    height: 10rem; /* Responsive size */
    border-radius: 50%;
    top: 1rem; /* Adjust to keep it consistent across devices */
    right: 2rem; /* Align to the right, responsive adjustment */
}

/* Media Queries for Smaller Devices */
@media (max-width: 768px) {
    h1 {
        font-size: 2rem;
        top: 10%; /* Adjust spacing for smaller screens */
    }

    .my-outer-box {
        height: 80%;
    }

    .profile-pic {
        width: 8rem;
        height: 8rem;
        top: 1.5rem;
        right: 1.5rem;
    }

    .my-inner-box {
        height: 50%;
    }
}

@media (max-width: 480px) {
    h1 {
        font-size: 1.5rem;
        top: 12%; /* Adjust spacing for very small screens */
    }

    p {
        font-size: 0.9rem;
    }

    .profile-pic {
        width: 6rem;
        height: 6rem;
    }
}
